.bb-payment-card-component {
  display: inline-block;
  width: 100%;
  min-width: 300px;
  max-width: 320px;

  .locked-icon {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    color: $white;
    z-index: 1;
  }

  .overlay::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba($black, 0.6);
    border-radius: 0.5rem;
  }
}



