bb-icon-ui {
  display: inline-flex;
}

.bicon {
  @extend .material-icons;

  &.animate {
    @extend .bb-loading-indicator-circle;
  }
}

@each $bb-icon-name, $icon-name in $icon-map {
  .bicon-#{$bb-icon-name}:before {
    content: $icon-name;
  }
}

@each $size-abbr, $size in $icon-size-map {
  .bicon.bicon-#{$size-abbr} {
    font-size: #{$size};
  }
  //to make btn /w icons a circle
  //TODO: bb-icon-ui should have ngClass .circle-bicon, passed from bb-button-ui, to ensure same height and width of icon.
  .btn-circle {
    i.circle-bicon-form {
      height: $size;
      width: $size;
    }
  }
}

.bicon.icon-circle {
  width: 3rem;
  height: 3rem;
  color: $white;
  line-height: 3rem;
  border-radius: 50%;
  text-align: center;
}

//TODO: Add feature @Input for bb-icon-ui
.bicon-arrow-in:before {
  content: 'reply';
  display: inline-block;
  transform: rotateX(180deg)
}

.bicon-arrow-out:before {
  content: 'reply';
  display: inline-block;
  transform: rotateY(180deg);
}



