

// Shim For legacy FA usages
// Maps all used fa-* to Material equiv.
// FA library removed -- below are the shims on existing FA
.fa, .fas {
  @extend .bicon;

  // Size
  &.fa-lg {
    @extend .bicon-md;
  }

  &.fa-2x {
    @extend .bicon-lg;
  }

  &.fa-4x {
    @extend .bicon-xl;
  }

  &.fa-5x {
    @extend .bicon-xxl;
  }

  // Icons
  &.fa-exclamation-triangle:before {
    content: 'report_problem'; //bicon for report-problem already reserved under 'error'
  }
  &.fa-bar-chart {
    @extend .bicon-currency-trade;
  }
