.far {
  @extend .bicon;

  &.fa-star:before {
    @extend .bicon-star-border;
  }
}

//WC2-exclusive bicon variants
//Matching bicon-* class names will defer to their WC3 versions (e.g. .bicon-error, .bicon-warning)

.bicon {
  &.bicon-alert {
    @extend .bicon-error;
  }

  &.bicon-drag-n-drop {
    @extend .bicon-menu;
  }

  &.bicon-authorized {
    @extend .bicon-authorizations;
  }
}

.glyphicon {
  @extend .bicon;
}
.glyphicon-chevron-left {
