.budget-card-wrapper {
  max-width: $budget-card-width;
  min-width: $budget-card-width;

  .budget-card-title { font-weight: $font-weight-medium; }
  .budget-card-category-icon {
    color: color("neutrals", "dark");
  }

  .budget-amount {
    color: color("neutrals", "darkest");
  }
  .progress-indicator-donut-overlay {
    height: 90%;
    width: 90%;
  }

  .label-amount-left { @extend .lead; }
  .budget-amount-label { @extend .font-weight-medium; }

  .budget-card-options-btn, .amount-regular-color .amount {
    color: inherit;
  }
}



