/**
 * Modifier that fades out the background to its normal state
 * for a list-group-item
 *
 * @example:
 * <ul class="list-group">
 *   <li class="list-group-item list-group-item-fade-success">
 *     ...
 *   </li>
 *   <li class="list-group-item">
 *     ...
 *   </li>
 * </ul>
 */
@keyframes fade-list-group-item-fade-success {
  from {
    background-color: #d9ede4; }
  to {
    background-color: inherit; } }

.list-group-item-fade-success {
  background-color: #d9ede4;
  animation-name: fade-list-group-item-fade-success;
  animation-fill-mode: forwards;
  animation-delay: 3s;
  animation-duration: 2s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in; }

@keyframes fade-list-group-item-fade-info {
  from {
    background-color: #ddeaf6; }
  to {
    background-color: inherit; } }

.list-group-item-fade-info {
  background-color: #ddeaf6;
  animation-name: fade-list-group-item-fade-info;
  animation-fill-mode: forwards;
  animation-delay: 3s;
  animation-duration: 2s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in; }

